.App-container{
    $clock_height: 300px;
    $clock_width: $clock_height;
    $second_hand_height: 4px;
    $second_hand_color: red;
    $minute_hand_height: 8px;
    $minute_hand_color: blue;
    $hour_hand_height: 12px;
    $hour_hand_color: yellow;
    $btn_hand_height: 18px;
    .pokemon-container{
        max-width: 900px;
        margin: 15px auto;
        width: 100%;
        .pokemon-list{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
            .pokemon-item{
                width: 270px;
                height: 310px;
                padding: 15px;
                box-shadow: 0 0 10px #ccc;
                margin-bottom: 30px;
                border-radius: 6px;
                color: #000;
                .pokemon-id,
                .pokemon-name{
                    font-weight: 600;
                    text-align: center;
                    text-transform: capitalize;
                }
                .pokemon-img{
                    width: 100%;
                }
            }
        }
        .pagination{
            display: flex;
            justify-content: center;
            font-weight: 600;
            &>div,
            &>a{
                margin: 0 10px;
                padding: 10px;
                cursor: pointer;
                border: 1px solid #000;
                border-radius: 2px;
                &.disabled{
                    opacity: 0.5;
                    cursor: default;
                }
            }
        }
        .pokemon-detail-view{
            margin-bottom: 15px;
            .pokemon-item{
                padding: 15px;
                border-radius: 15px;
                box-shadow: 0 0 10px #ccc;
                .pokemon-name{
                    text-transform: capitalize;
                    font-weight: 600;
                    text-align: center;
                }
                .pokemon-img{
                    margin: auto;
                    width: 100%;
                    max-width: 300px;
                    display: block;
                }
                .pokemon-thumbs{
                    display: flex;
                    justify-content: center;
                    .pokemon-thumb-img{
                        width: 100%;
                        max-width: 100px;
                        margin: 0 10px;
                        border-radius: 8px;
                        border: 1px solid #000;
                        box-shadow: 0 0 10px #7d7b7b;
                    }
                }
                .abilities-container{
                    display: flex;
                    flex-wrap: wrap;
                    .abilities-text{
                        text-transform: capitalize;
                        &:not(:last-child){
                            padding-right: 5px;
                            &:after{
                                content: ",";
                            }
                        }
                    }
                }
            }
        }
    }
}