.App-container{
    $clock_height: 300px;
    $clock_width: $clock_height;
    $second_hand_height: 4px;
    $second_hand_color: red;
    $minute_hand_height: 8px;
    $minute_hand_color: blue;
    $hour_hand_height: 12px;
    $hour_hand_color: yellow;
    $btn_hand_height: 18px;
    .digital-clock-container{
        background-color: #ccc;
        position: relative;
        display: flex;
        overflow: hidden;
        height: 96px;
        font-weight: 500;
        font-size: 5em;
        text-align: center;
        border-radius: 10px;
        .digital-figure-container{
            min-width: 92px;
            padding: 0 10px;
        }
        .clock-divider{
            animation: blinker 1s linear infinite;
        }
        @keyframes blinker {
            50% {
                opacity: 0;
            }
        }
    }
    .date-container {
        padding: 5px;
        font-weight: 600;
    }
}